<script>
import anime from 'animejs'
import Intro from './components/Intro'

export default {
  name: 'App',
  components: {
    Intro
  },
  mounted: function () {
    // config click event
    document.addEventListener('click', this.clickEffect)
    const self = this
    const logo = this.$el.querySelector('#logo')
    if (this.$route.name !== 'Intro') {
      logo.parentNode.removeChild(logo)
      return
    }
    // add logo line animation
    anime({
      targets: 'svg path, svg circle',
      strokeDashoffset: [anime.setDashoffset, 0],
      easing: 'easeInOutCubic',
      duration: 3000,
      delay: function (el, i) { return 0 },
      begin: function (anim) {
        self.$el.querySelectorAll('.cls-1').forEach(target => {
          target.classList.add('fill-logo')
        })
        self.$el.querySelectorAll('.cls-2').forEach(target => {
          target.classList.add('fill-logo')
        })
        self.$el.querySelectorAll('.cls-3').forEach(target => {
          target.classList.add('fill-logo')
        })
      },
      complete: function (anim) {
        setTimeout(function () {
          logo.classList.add('slide-up')
          const transEndEvent = function () {
            logo.removeEventListener('transitionend', transEndEvent)
            logo.parentNode.removeChild(logo)
          }
          logo.addEventListener('transitionend', transEndEvent)
        }, 1500)
      }
    })
  },
  methods: {
    clickEffect: function (e) {
      const d = document.createElement('div')
      d.className = 'clickEffect'
      d.style.top = `${e.clientY - 100}px`
      d.style.left = `${e.clientX - 100}px`
      document.body.appendChild(d)
      d.addEventListener('animationend', function () {
        d.parentElement.removeChild(d)
        d.removeEventListener('animationend', this)
      })
    }
  }
}
</script>

<style lang="scss">
  @keyframes clickEffect {
    0% {
      opacity: 1;
      transform: scale(0.1);
      border-width: 2.5em;
    }
    100% {
      opacity: 0.2;
      transform: scale(1);
      border-width: 0.03em;
    }
  }

  @keyframes dot-loading {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes fill-text {
    0% {
      fill-opacity: 0;
      stroke: #942323;
      stroke-width: 2px;
    }
    85% {
      fill-opacity: 0;
      stroke: #942323;
      stroke-width: 1.8px;
    }
    100% {
      fill-opacity: 1;
      stroke: white;
      stroke-width: 1px;
    }
  }

  body {
    margin: 0;
    padding: 0;
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
    overflow: hidden;
    margin: 0;
    padding: 0;
  }

  .clickEffect {
    position: fixed;
    width: 200px;
    height: 200px;
    box-sizing: border-box;
    border-style: solid;
    border-color: rgba(255, 255, 255, 1);
    border-radius: 50%;
    animation: clickEffect 0.4s ease-in-out;
    transform-origin: center;
    z-index: 99999;
    will-change: opacity, transform, border-width;
    pointer-events: none;
  }

  .cls-1, .cls-4 {
    fill: #942323;
  }
  .cls-1, .cls-2, .cls-3, .cls-4 {
    stroke-width: 1.5px;
    stroke: #942323;
    stroke-miterlimit: 10;
    fill-opacity: 0;
  }
  .cls-2{
    fill: #1e556d;
  }
  .cls-3{
    fill: #133180;
  }
  .cls-4{
    stroke-width: 0.5px;
  }

  #logo {
    position: fixed;
    overflow: hidden;
    width: 100%;
    max-height: 100vh;
    z-index: 9999;
    background-color: #d9d4b3;

    transition: max-height .8s ease;

    will-change: max-height;
  }
  #logo svg {
    width: 100vw;
    height: 100vh;
    display: block;
    margin: 0 auto;
    transform: translate(-20px, 0);
  }

  .slide-up {
    max-height: 0 !important;
  }

  .fill-logo {
    animation: fill-text 3s ease-out;
    animation-fill-mode: forwards;
  }
</style>



